@import "~terriajs-variables";
@import "~terriajs/lib/Sass/global/global";

body {
  margin: 0;
  overflow: hidden;
}

:global {
  @media print {
    .PPBCHA-print-disclaimer {
      color: #000;
      display: block;
    }
  }

  // To customise the look of your Terria Map, uncomment this section and make changes to your heart's content
  // You will also want to uncomment and change values in lib/Styles/variables.scss

  // Import any custom fonts here
  //@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,700);

  // Import any custom fonts here
  //@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,700);

  html {
    font-family: $font-base;
  }

  .tjs-branding__branding a {
    text-decoration: none;
  }

  // Logo area in top left
  .tjs-branding__branding {
    a:first-child,
    a:last-child {
      display: none;

      @media screen and (min-width: $sm) {
        display: inline;
      }
    }
    // Different font size for the branding
    font-weight: 300;
    color: white;
    font-size: 26px;

    // All kinds of buttons in various screens
    .tjs-_buttons__btn {
      &-primary {
        font-family: $font-base;
      }
      &:hover {
        color: $color-primary;
      }
    }

    // Buttons like "Share" within the map area.
    .tjs-_buttons__btn--map {
      background-color: $color-primary;
      &:focus,
      &:hover {
        color: white;
      }
    }

    // Lat/lon buttons
    .tjs-legend__locationBar,
    .tjs-legend__distanceLegend {
      background: $color-primary;
      color: white;
      &:hover,
      &:focus {
        background: $color-primary;
        color: white;
      }
    }

    // Progress bar while datasets are loading
    .tjs-progress-bar__progress-bar {
      background: $color-primary;
    }

    // Flatten and simplify display of timeline controls
    .tjs-timeline-controls__timeline__control {
      background: $dark-lighter;
      border: none;
    }

    .tjs-timeline__text-cell {
      font-family: $font-base;
    }

    .tjs-timeline__timeline {
      background: $dark-lighter;
    }

    .cesium-timeline-bar {
      background: $dark-with-overlay;
    }

    // Feature info table
    .tjs-feature-info-section__content {
      h1 {
        color: $color-primary;
      }
      table th {
        // font-family: $font-base; // if you don't want monospaced
        font-weight: 200; // not bold
        padding-right: 0.5em;
      }

      table td {
        border: none;
        // font-family: $font-base; // if you don't want monospaced
        font-weight: normal; // not bold
        color: hsl(0, 0%, 90%);
      }
      a {
        color: $color-secondary;
      }
    }
    // The Data Catalog ("Add Data") page
    // expandable catalog groups
    .tjs-data-catalog-group__btn--catalog:hover {
      background-color: $color-secondary !important;
      color: white;
    }
    .tjs-data-catalog-group__btn--catalog:focus {
      background-color: $color-primary !important;
      color: white;
    }

    // The currently selected item in the list
    .tjs-data-catalog-item__btn-catalog-item--is-previewed {
      &:focus {
        color: $color-primary;
      }
    }
    // The tabs at the top
    .tjs-tabs__btn--tab {
      font-family: $font-base;
    }

    .tjs-tabs__btn--selected {
      color: $color-primary-dark;
    }
    .tjs-tabs__btn--tab:hover {
      color: $color-secondary;
    }

    .tjs-data-catalog-group__isPreviewed {
      background: #686de0;
      color: #ffffff;
    }
  }

  .tjs-terria-viewer-wrapper__cesium-container .cesium-widget-credits {
    bottom: 3px;
    left: unset;
    right: 0;
    text-align: right;
  }

  .cesium-credit-textContainer {
    border-top-left-radius: 2px;
  }

  .cesium-credit-textContainer,
  .cesium-credit-expand-link {
    opacity: 0.5;
    background: rgb(255, 255, 255);
    padding: 3px;
    color: #686de0;

    transition: opacity linear 0.2s;
  }

  .cesium-credit-logoContainer {
    opacity: 0.5;
    margin-bottom: 7px;
    padding-bottom: 7px;
    transition: opacity linear 0.2s;
  }

  .cesium-credit-logoContainer:hover {
    opacity: 1;
  }

  .cesium-timeline-bar {
    background: $dark-darker !important;
  }
}
